<template>
  <div class="register">
    <div class="form">
      <h1>Ê≥®ÂÜå</h1>

      <el-form
        size="mini"
        label-position="top"
        :model="registerUser"
        status-icon
        :rules="rules"
        ref="registerUser"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="Áî®Êà∑Âêç" prop="name">
          <el-input type="text" v-model="registerUser.name" placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"></el-input>
        </el-form-item>

        <el-form-item label="ÈÇÆÁÆ±" prop="email">
          <el-input type="email" v-model="registerUser.email" placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"></el-input>
        </el-form-item>

        <el-form-item label="ÂØÜÁ†Å" prop="password">
          <el-input type="password" v-model="registerUser.password" placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"></el-input>
        </el-form-item>

        <el-form-item label="Á°ÆËÆ§ÂØÜÁ†Å" prop="checkPassword">
          <el-input type="password" v-model="registerUser.checkPassword" placeholder="ËØ∑Á°ÆËÆ§ÂØÜÁ†Å"></el-input>
        </el-form-item>

        <el-form-item label="Ë∫´‰ªΩ" prop="role">
          <el-select v-model="registerUser.role" placeholder="ËØ∑ÈÄâÊã©Ë∫´‰ªΩ">
            <el-option label="ÁÆ°ÁêÜÂëò" value="admin"></el-option>
            <el-option label="ÂëòÂ∑•" value="employee"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('registerUser')">Ê≥®ÂÜå</el-button>
          <el-button type="warning" @click="resetForm('registerUser')">ÈáçÁΩÆ</el-button>
          <el-button @click="login">ËøîÂõûÁôªÂΩï<i class="el-icon-right"></i></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    var validateName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"));
      } else {
        if (value.length < 2 || value.length > 20) {
          callback(new Error("ÈïøÂ∫¶Â∫îÂú®2~20‰πãÈó¥"));
        }
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"));
      } else {
        if (
          /^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/.test(
            value
          )
        ) {
          callback();
        }
        callback(new Error("ÈÇÆÁÆ±Ê†ºÂºèÊúâËØØ"));
      }
    };
    var validatePassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("ËØ∑ËæìÂÖ•ÂØÜÁ†Å"));
      } else {
        if (value.length < 8 || value.length > 16) {
          callback(new Error("ÂØÜÁ†ÅÈïøÂ∫¶Â∫îÂú®8~16‰Ωç"));
        }

        if (this.registerUser.checkord !== "") {
          this.$refs.registerUser.validateField("checkPassword");
        }

        callback();
      }
    };
    var validatePassword2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å"));
      } else if (value !== this.registerUser.password) {
        callback(new Error("ÂØÜÁ†Å‰∏ç‰∏ÄËá¥!"));
      } else {
        callback();
      }
    };
    var validateRole = (rule, value, callback) => {
      if (value == "") {
        callback(new Error("ËØ∑ÈÄâÊã©Ë∫´‰ªΩ"));
      } else {
        callback();
      }
    };
    return {
      registerUser: {
        name: "",
        email: "",
        role: "",
        password: "",
        checkPassword: ""
      },
      rules: {
        name: [{ validator: validateName, trigger: "blur" }],
        email: [{ validator: validateEmail, trigger: "blur" }],
        password: [{ validator: validatePassword, trigger: "blur" }],
        checkPassword: [{ validator: validatePassword2, trigger: "blur" }],
        role: [{ validator: validateRole, trigger: "blur" }]
      }
    };
  },
  methods: {
    login() {
      this.$router.push("/login");
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.loading = true;
          axios
            .post("/api/users/register", this.registerUser)
            .then(res => {
              // this.loading = false;
              this.$message({
                  message: "Ê≥®ÂÜåÊàêÂäü,Âç≥Â∞ÜË∑≥ËΩ¨Ëá≥ÁôªÂΩïÈ°µ",
                  type: "success"
                });
              setTimeout(() => {
                this.$router.push('/login')
              }, 2000);
            })
            .catch(err => {
              console.log('err')
              this.loading = false;
              if (err.response.status === 409) {
                this.$message({
                  message: "ËØ•ÈÇÆÁÆ±Â∑≤ÁªèË¢´Ê≥®ÂÜåü§£",
                  type: "error"
                });
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

